body {
  position: relative;
  background-color: #071321;
  overflow: hidden;
  color: #2d2d2d;
  font-family: "Montserrat", sans-serif;
}

.veryImportantLink {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 14px;
  color: white;
  text-decoration: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
