html,
body {
  /* font-family: "Commissioner", sans-serif; */
  font-family: 'Montserrat', sans-serif;
  line-height: 1.65;
}

.item {
  z-index: 2;
}

a {
  color: #c6ac8f;
  text-decoration: none;
}

.slider {
  height: 100vh;
  width: 100vw;
  background-color: #0a0908;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  position: relative;
  overflow: hidden;
  transition: background-color 2s;
}

.slider .credits {
  position: absolute;
  top: 2rem;
  left: 2rem;
  line-height: 1.65;
  z-index: 10;
  max-width: 320px;
}

.slider .item .imgs {
  position: relative;
  width: 60%;
  padding-top: 60%;
}

.slider .item .imgs .grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(12, 1fr);
  grid-column-gap: 32px;
  grid-row-gap: 32px;
  transform: rotate(-20deg);
  opacity: 1;
}

.slider .item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider .item .img {
  width: 100%;
  height: 100%;
  position: relative;
  will-change: transform;
  will-change: opacity;
}

img {
  border-radius: 20px;
  /* clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%); */
}

.slider .item .img img {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  position: relative;
  filter: contrast(110%) brightness(110%) saturate(130%);
}

.slider .item .img img::before {
  content: "";
  display: block;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  pointer-events: none;
  mix-blend-mode: screen;
  background: rgba(243, 106, 188, 0.3);
}

.slider .item .img-1 {
  grid-area: 1/1/7/5;
}

.slider .item .img-2 {
  grid-area: 2/5/7/13;
}

.slider .item .img-3 {
  grid-area: 7/1/12/9;
}

.slider .item .img-4 {
  grid-area: 7/9/13/13;
}

.slider .item .content {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1.15;
  /* font-size: 8rem; */
  font-weight: 700;
}

.slider .item .content .wrap {
  text-align: center;
  text-shadow: 5px 5px 4px rgba(10, 9, 8, 0.2);
  width: 100%;
  max-width: 800px;
  line-height: 1;
}

.wrap1 {
  text-align: center;
  text-shadow: 5px 5px 4px rgba(10, 9, 8, 0.2);
  width: 100%;
  max-width: 800px;
  line-height: 1;
}

.slider .item .content .wrap .letter {
  display: inline-block;
}

.wrap1 {
  display: inline-block;
}

.slider .nav1 .next,
.slider .nav1 .prev {
