  width: 48%;
}

.demo-class__info__title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 10px;
}

.demo-class__info__value {
  font-size: 18px;
  line-height: 1.5;
}

.demo-class__cta {
  display: inline-block;
  padding: 12px 24px;
